@use 'variaveis';
@use 'project';
@use 'header';
@use 'home';
@use 'tech';
@use 'about';
@use 'contact';
@use 'submit';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: #ECECF1;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  background-image: url(../images/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;

  @media (max-width: 768px) {
  }
}

  .container {
      max-width: 1980px;
      width: 100%;
      margin: 0 auto;
  }

/* Estilo da Scrollbar */

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: #060E10;
}

::-webkit-scrollbar-thumb {
  background-color: #AAA;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #888;
}

footer {
  color: #fff;
  text-align: center;
  padding: 20px;
  bottom: 0;
  width: 100%;
  height: 30vh;
  font-family: 'Poppins', sans-serif;

  p {
    margin-top: 100px;
    font-size: 1.4rem;
  }
}

@media (max-width: 767px) {
  footer {
    margin: 0;
    height: 100%;

    p {
      font-size: 1rem;
      margin: 0;
    }
  }
}
